.App {
  text-align: center;
  background-color: rgb(228 231 209 / 24%);
  height: 100vh;
  animation: color-change 10s linear 5s infinite alternate both;
}

@keyframes color-change {
  0% {
    background: #E6F4F1
  }
  25% {
    background: #C5E1DE
  }
  50% {
    background: #BAD1E6
  }
  75% {
    background: #F3F0F2
  }
  100% {
    background: #E7EDF7
  }
}

canvas{
  width: 100%;
  height: 100%;
}
.custom-shape-divider-bottom-1671024102 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom-shape-divider-bottom-1671024102 svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 210px;
}

.custom-shape-divider-bottom-1671024102 .shape-fill {
  fill: #306FB8;
}
